import store from "@/store";
import Ws from "@adonisjs/websocket-client";
import HTTP from "@/libs/http";
import notify from "@/libs/notify";
import {
  bow_center_generate_names,
  bow_arm_generate_names,
  bow_arrow_generate_names
} from "@/libs/helpers";

const ws = Ws(`ws://${window.location.hostname}:3333`);
console.log(`ws://${window.location.hostname}:3333`);
ws.connect();
const chat = ws.subscribe("measurement");
chat.on("message", message => {
  store.dispatch("measurementModule/messageReceived", message);
});

import moment from "moment";

export default {
  namespaced: true,
  state: {
    messages: [],
    samples: 1000,
    items: [],
    last_measurement: null
  },
  getters: {
    messagesR: state => {
      return state.messages.slice().reverse();
    },

    items_with_generated_names: state => {
      return state.items.map(item => {
        item.bow_center.generated_name = bow_center_generate_names(
          item.bow_center
        );
        item.bow_arm.generated_name = bow_arm_generate_names(item.bow_arm);
        item.bow_arrow.generated_name = bow_arrow_generate_names(
          item.bow_arrow
        );
        return item;
      });
    }
  },
  actions: {
    async messageReceived({ commit, state, dispatch }, message) {
      message.date = moment();
      commit("APPEND_MESSAGE", message);

      if (message.phase === "DATA_SAVED_TO_DATABASE") {
        // get all measurements again
        await dispatch("fetchItems");

        // find the last one
        const measurement = state.items.find(m => {
          return message.measurement_id == m.id;
        });

        // put to the last_measurmenent
        commit("SET_LAST_MEASUREMENT", measurement);
      }
    },
    startMeasurement({ commit, state, rootState }, payload) {
      ws.getSubscription("measurement").emit("message", {
        sender: "commander",
        phase: "START",
        samples: state.samples,
        person: rootState.personModule.current,
        bowArrow: rootState.bowArrowModule.current,
        bowArm: rootState.bowArmModule.current,
        bowCenter: rootState.bowCenterModule.current
      });
    },
    async fetchItems({ commit }) {
      try {
        const { data } = await HTTP().get(`/measurements`);
        commit("SET_ITEMS", data);
      } catch (err) {
        notify({
          color: "error",
          text: "Unable to fetch items.",
          error: err
        });
      }
    },

    async saveAdditionalData({ commit }, item) {
      try {
        const { data } = await HTTP().post(`/measurements/${item.id}`, item);
        commit("SET_LAST_MEASUREMENT", null);
        notify({
          color: "green",
          text: "Additional data saved."
        });
      } catch (err) {
        notify({
          color: "error",
          text: "Unable to save item additional data.",
          error: err
        });
      }
    }
  },
  mutations: {
    SET_MESSAGES(state, messages) {
      state.messages = messages;
    },

    APPEND_MESSAGE(state, message) {
      state.messages.push(message);
    },

    CHANGE_SAMPLES(state, samples) {
      state.samples = samples;
    },

    SET_ITEMS(state, items) {
      state.items = items;
    },

    SET_LAST_MEASUREMENT(state, item) {
      state.last_measurement = item;
    },

    APPEND_ITEM(state, item) {
      state.items.push(item);
    },

    REMOVE_ITEM(state, item) {
      state.items.splice(state.items.findIndex(i => i.id === item.id), 1);
    }
  }
};
