import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'root',
      redirect: { name: 'measurements.index' },
    },
    {
      path: '/do-measurement',
      name: 'measurements.index',
      component: resolve => require(['@/views/DoMeasurement.vue'], resolve),
    },
    {
      path: '/results',
      name: 'measurements.results',
      component: resolve => require(['@/views/Results.vue'], resolve),
    },
    {
      path: '/persons',
      name: 'persons.index',
      component: resolve => require(['@/views/Persons.vue'], resolve),
    },
    {
      path: '/bow-arms',
      name: 'bow.arms',
      component: resolve => require(['@/views/BowArms.vue'], resolve),
    },
    {
      path: '/bow-centers',
      name: 'bow.centers',
      component: resolve => require(['@/views/BowCenters.vue'], resolve),
    },
    {
      path: '/bow-arrows',
      name: 'bow.arrows',
      component: resolve => require(['@/views/BowArrows.vue'], resolve),
    },
    {
      path: '/page-not-found',
      name: 'page-not-found',
      component: resolve => require(['@/views/PageNotFound.vue'], resolve),
    },
    {
      path: '/*',
      redirect: '/page-not-found',
    },
  ],
});
