import HTTP from '@/libs/http';
import notify from '@/libs/notify';

export default {
  namespaced: true,
  state: {
    dialogOpened: false,
    items: [],
  },
  getters: {},
  actions: {
    async compareItems({ commit }, items) {
      await items.forEach(async item => {
        try {
          const { data } = await HTTP().get(`/measurements/${item.id}`);
          item.data = data;
          commit('SET_ITEMS', items);
        } catch (err) {
          notify({
            color: 'error',
            text: 'Unable to fetch items.',
            error: err,
          });
        }
      });

      commit('OPEN_DIALOG');
    },
  },
  mutations: {
    SET_ITEMS(state, items) {
      state.items = items;
    },

    OPEN_DIALOG(state) {
      state.dialogOpened = true;
    },

    CLOSE_DIALOG(state) {
      state.items = [];
      state.dialogOpened = false;
    },
  },
};
