export const bow_center_generate_names = item => {
  return `${item.brand} - ${item.model} [WGT ${item.weight},AUP ${item.angle_up},ADN ${item.angle_down}, ILFUP ${item.ilf_berger_distance_up}, ILFDN ${item.ilf_berger_distance_down}] `;
};

export const bow_arm_generate_names = item => {
  return `${item.brand} - ${item.model} [FPE ${item.font_per_energy},LNG ${item.length}]`;
};

export const bow_arrow_generate_names = item => {
  return `${item.brand} - ${item.model} [SPN ${item.spin},GRN ${item.grain},LNG ${item.length}, WGT ${item.weight}]`;
};
