import Vue from 'vue';

import HTTP from '@/libs/http';
import notify from '@/libs/notify';
import { bow_arrow_generate_names } from '@/libs/helpers';

export default {
  namespaced: true,
  state: {
    current: null,
    items: [],
    createDialogOpened: false,
    editDialogOpened: false,
    deleteDialogOpened: false,
  },
  getters: {
    bow_arrows_with_generated_names: state => {
      return state.items.map(item => {
        item.generated_name = bow_arrow_generate_names(item);
        return item;
      });
    },
  },
  actions: {
    async fetchItems({ commit }) {
      try {
        const { data } = await HTTP().get(`/bow-arrows`);
        commit('SET_ITEMS', data);
      } catch (err) {
        notify({
          color: 'error',
          text: 'Unable to fetch items.',
          error: err,
        });
      }
    },

    async createItem({ commit }, item) {
      try {
        const { data } = await HTTP().post(`/bow-arrows`, item);
        commit('APPEND_ITEM', data);
        commit('CLOSE_CREATE_DIALOG');
        notify({
          color: 'green',
          text: 'Item created.',
        });
      } catch (err) {
        notify({
          color: 'error',
          text: 'Unable to create item.',
          error: err,
        });
      }
    },

    async editItem({ commit }, item) {
      try {
        const { data } = await HTTP().patch(`/bow-arrows/${item.id}`, item);

        const pack = {
          old: item,
          new: data,
        };

        commit('REPLACE_ITEM', pack);
        commit('CLOSE_EDIT_DIALOG');

        notify({
          color: 'green',
          text: 'Item saved.',
        });
      } catch (err) {
        notify({
          color: 'error',
          text: 'Unable to edit item.',
          error: err,
        });
      }
    },

    async deleteItem({ commit }, item) {
      try {
        const { data } = await HTTP().delete(`/bow-arrows/${item.id}`);
        commit('REMOVE_ITEM', data);
        commit('CLOSE_DELETE_DIALOG');
        notify({
          color: 'green',
          text: 'Item deleted.',
        });
      } catch (err) {
        notify({
          color: 'error',
          text: 'Unable to delete item.',
          error: err,
        });
      }
    },
  },
  mutations: {
    OPEN_CREATE_DIALOG(state) {
      state.createDialogOpened = true;
    },

    CLOSE_CREATE_DIALOG(state) {
      state.createDialogOpened = false;
    },

    OPEN_EDIT_DIALOG(state, item) {
      state.current = item;
      state.editDialogOpened = true;
    },

    CLOSE_EDIT_DIALOG(state) {
      state.current = null;
      state.editDialogOpened = false;
    },

    OPEN_DELETE_DIALOG(state, item) {
      state.current = item;
      state.deleteDialogOpened = true;
    },

    CLOSE_DELETE_DIALOG(state) {
      state.current = null;
      state.deleteDialogOpened = false;
    },

    SET_ITEMS(state, items) {
      state.items = items;
    },

    SET_CURRENT(state, item) {
      state.current = item;
    },

    APPEND_ITEM(state, item) {
      state.items.push(item);
    },

    REMOVE_ITEM(state, item) {
      state.items.splice(state.items.findIndex(i => i.id === item.id), 1);
    },

    REPLACE_ITEM(state, pack) {
      Vue.set(state.items, state.items.findIndex(i => i.id === pack.old.id), pack.new);
    },
  },
};
