export default {
    namespaced: true,
    state: {
       text: '',
       color: 'error',
       timeout: 5000,
    },
    mutations: {
        SET_NOTIFICATION(state, notification) {
            state.text = notification.text;
            state.color = notification.color;
        },
    },
    actions: {
        showNotification({ commit, state }, notification) {
            commit('SET_NOTIFICATION', notification)
            setTimeout(() => commit('SET_NOTIFICATION', ''), state.timeout);
        }
    }
}
